html {
  margin: 0;
  padding: 0;
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  position: relative;
  overflow-x: hidden;
  min-width: 320px;
  font-family: "OpenSansRegular", sans-serif;
  font-size: 16px;
  line-height: 1.42;
  color: #000000;
  opacity: 1;
}
body input,
body textarea {
  outline: none;
  border: #666666 1px solid;
}
body input:required:valid,
body textarea:required:valid {
  border-color: #008000;
}
body input:focus:required:invalid,
body textarea:focus:required:invalid {
  border-color: #ff0000;
}

.img-responsive {
  display: block;
  height: auto;
  max-width: 100%;
}
.img-responsive.img-width {
  width: 100%;
}

